import {
	AfterViewInit,
	Component,
	ElementRef,
	Input,
	ViewChild,
	ContentChild,
	AfterContentInit,
	ViewEncapsulation,
} from '@angular/core';

import { LeafletMap } from '../../classes/leaflet-map';
import { LeafletFullscreenControlComponent } from '../controls/leaflet-fullscreen-control/leaflet-fullscreen-control.component';
import { LeafletZoomControlComponent } from '../controls/leaflet-zoom-control/leaflet-zoom-control.component';
import { LeafletLocateControlComponent } from '../controls/leaflet-locate-control/leaflet-locate-control.component';
import { LeafletDragControlComponent } from '../controls/leaflet-drag-control/leaflet-drag-control.component';
import { LeafletDrawControlComponent } from '../controls/leaflet-draw-control/leaflet-draw-control.component';

@Component({
	selector: 'aui-leaflet',
	template: `<div class="aui-leaflet" [ngClass]="{'is-full-screen': leafletMap.fullScreen}">
	<div class="aui-leaflet__content" [ngClass]="{'has-content': hasSidebar}" #content>
		<ng-content></ng-content>
	</div>
	<div class="aui-leaflet__wrapper">
		<div #map class="aui-leaflet__map"></div>
		<div class="aui-leaflet__controls aui-leaflet__controls--top-left">
			<ng-content select="[controls][top][left]"></ng-content>
		</div>
		<div class="aui-leaflet__controls aui-leaflet__controls--top-right">
			<ng-content select="[controls][top][right]"></ng-content>
		</div>
		<div class="aui-leaflet__controls aui-leaflet__controls--bottom-right">
			<ng-content select="[controls][bottom][right]"></ng-content>
		</div>
		<div class="aui-leaflet__controls aui-leaflet__controls--bottom-left">
			<ng-content select="[controls][bottom][left]"></ng-content>
		</div>
	</div>
</div>
`,
	styles: [`.aui-leaflet{border:1px solid #b0b0b0;display:flex;height:600px;width:100%}.aui-leaflet__wrapper{flex:1;height:100%;overflow:hidden;position:relative}.aui-leaflet.is-full-screen{border:none;bottom:0;position:fixed;height:100%;left:0;right:0;top:0;z-index:10}.aui-leaflet__map{font-size:inherit;font-family:inherit;height:100%;position:relative;z-index:1}.aui-leaflet__content{background-color:#fff;overflow:auto;width:0}.aui-leaflet.is-full-screen .aui-leaflet__content{border:1px solid #b0b0b0;box-shadow:7px 7px 0 rgba(0,0,0,.1);position:absolute;left:20px;max-height:calc(100% - 160px);top:20px;width:350px;z-index:2}.aui-leaflet__content.has-content{border-right:1px solid #b0b0b0;padding:20px;width:300px}.aui-leaflet__controls{position:absolute;z-index:2}.aui-leaflet__controls--bottom-left{bottom:20px;left:20px}.aui-leaflet__controls--bottom-right{bottom:20px;right:20px}.aui-leaflet__controls--top-left{left:20px;top:20px}.aui-leaflet.is-full-screen .aui-leaflet__controls--top-left{left:390px}.aui-leaflet__controls--top-right{right:20px;top:20px}.aui-leaflet__control{float:left}.aui-leaflet__controls--top-left .aui-leaflet__control,.aui-leaflet__controls--top-right .aui-leaflet__control{margin-bottom:5px}.aui-leaflet__controls--bottom-left .aui-leaflet__control,.aui-leaflet__controls--bottom-right .aui-leaflet__control{margin-top:5px}.aui-leaflet__controls--bottom-left .aui-leaflet__control,.aui-leaflet__controls--top-left .aui-leaflet__control{margin-right:5px}.aui-leaflet__controls--bottom-right .aui-leaflet__control,.aui-leaflet__controls--top-right .aui-leaflet__control{margin-left:5px}.aui-leaflet__zoom-control{display:block}.aui-leaflet__html-icon{background-color:transparent;border:none}.leaflet-popup-content-wrapper{border:1px solid #f3f3f3!important;border-radius:0!important;box-shadow:.5rem .5rem 0 rgba(0,0,0,.1)!important;position:relative}.leaflet-popup-content-wrapper::after{content:'';position:absolute;bottom:-1px;height:1px;background-color:#fff;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);width:22px}.leaflet-popup-content{margin:10px!important;font-size:14px}.leaflet-container{font-family:inherit!important}.leaflet-popup-close-button{right:5px!important;top:5px!important;z-index:1}`], // @todo: move this to aui-kit/core branding? check with styleguide team
	encapsulation: ViewEncapsulation.None,
})
export class LeafletComponent implements AfterViewInit, AfterContentInit {
	@ViewChild('map') map: ElementRef;
	@ViewChild('content') content: ElementRef;
	@ContentChild(LeafletFullscreenControlComponent) fullScreenControl: LeafletFullscreenControlComponent;
	@ContentChild(LeafletZoomControlComponent) zoomControl: LeafletZoomControlComponent;
	@ContentChild(LeafletLocateControlComponent) locateControl: LeafletLocateControlComponent;
	@ContentChild(LeafletDragControlComponent) dragControl: LeafletDragControlComponent;
	@ContentChild(LeafletDrawControlComponent) drawControl: LeafletDrawControlComponent;
	@Input() leafletMap: LeafletMap;
	@Input() hasSidebar;

	ngAfterViewInit() {
		// Make sure the map is properly rendered before initializing it
		setTimeout(() => {
			this.leafletMap.init(this.map.nativeElement);
		});
	}

	ngAfterContentInit() {
		[
			this.fullScreenControl,
			this.zoomControl,
			this.locateControl,
			this.dragControl,
			this.drawControl,
		].forEach(control => control ? control.map = this.leafletMap : null);
	}
}
